import React from "react";
import { Link } from "react-router-dom";

import {LANDING_ROUTE, LOGIN_ROUTE} from "../../constants";

export const Welcome = () => {
  return (
    <>
      <h1 className="mt-5 text-center">Please select necessary route:</h1>
      <div className="mt-5 text-center">
        <Link to={process.env.PUBLIC_URL + LANDING_ROUTE} className="font-weight-bold" >Landing (first part)</Link>&nbsp;&nbsp;
        <Link to={process.env.PUBLIC_URL + LOGIN_ROUTE} className="font-weight-bold" >Login (second part)</Link>
      </div>
    </>
  );
};