import React from "react";
import { Redirect, Route, Switch } from "react-router-dom";

import {
  HOME_ROUTE,
  LANDING_ROUTE,
  LOGIN_ROUTE,
  USER_INFO_ROUTE,
  USERS_LIST_ROUTE
} from "constants/routes";

import { UsersList } from "components/UsersList";
import { UserInfo } from "components/UserInfo";
import { Landing } from "components/Landing";
import { LoginPage } from "components/Login";
import { Welcome } from "components/Welcome";

import { PrivateRoute } from "containers/PrivateRoute";

export const Routes = () => {
  console.log(process.env.PUBLIC_URL)
  return (
    <Switch>
      <Route exact={true} path={process.env.PUBLIC_URL + HOME_ROUTE} component={Welcome} />
      <Route exact={true} path={process.env.PUBLIC_URL + LANDING_ROUTE} component={Landing} />
      <Route exact={true} path={process.env.PUBLIC_URL + LOGIN_ROUTE} component={LoginPage} />
      <PrivateRoute exact={true} path={process.env.PUBLIC_URL + USERS_LIST_ROUTE} component={UsersList}/>
      <PrivateRoute exact={true} path={process.env.PUBLIC_URL + USER_INFO_ROUTE} component={UserInfo} />

      {/* Default route */}
      <Route>
        <Redirect to={process.env.PUBLIC_URL + HOME_ROUTE} />
      </Route>
    </Switch>
  );
};
